    export let file_blob
    export let file_type
    export let file_name
    export let file_uncompressed
    import { afterUpdate } from 'svelte'

    export let onclosed = () => {
    };


    let serverstatusurl;
    let serverIsAvailable=false
    let posenetselectoropen = false;
    let search3dopen = false;
    let scene3DGetPrimitive = false;
    let selectModifier = false;
    let reopendialogevent = false;
    let layerStack=[]
    let toY="100px"

    $:{
        if (!onPremise && bearer) {
            servertype='Gyre';
            serverurl="https://cloud.gyre.ai"
            serverstatusurl = serverurl  +"/"+bearer+ "/status";
        }
        if (serverurl || bearer && servertype) {
            if (serverurl.includes("status")) {
                serverstatusurl = serverurl 
            } else {
                if (bearer) {
                    serverstatusurl = serverurl.replace(/\/$/, "") +"/"+bearer+ "/status";
                    if(servertype=='COMFY'){
                        serverstatusurl = serverurl.replace(/\/$/, "") +"/"+bearer+ "/settings";
                    }

                } else {
                    serverstatusurl = serverurl.replace(/\/$/, "") + "/status";
                    if(servertype=='COMFY'){
                        serverstatusurl = serverurl.replace(/\/$/, "") + "/settings";
                    }
                }
            }



            if(servertype) {
                setTimeout(async () => {
                    let response
                    try {
                        response = await fetch(serverstatusurl);
                    } catch (e) {
                        serverIsAvailable = false
                    }
                    if (response?.ok) serverIsAvailable = true
                    else serverIsAvailable = false
                }, 0)
            }
             
        }
        toY="100px"
        if (wizard_glb.thumb)  {
            if (!wizard_image.base64)  {
                layerStack[0]=wizard_glb.thumb
                toY="100px"
            } else {
                layerStack[0]=wizard_image.base64
                layerStack[1]=wizard_glb.thumb
                toY="300px"
            }
        }
        if (wizard_background.base64) {
            layerStack[0]=wizard_image.base64
            layerStack[1]=wizard_background.base64
            toY="300px"
        }
        if (wizard_text.thumb)  {
            layerStack[0]=wizard_text.thumb
            toY="100px"
        }        
        if (wizard_pose.thumb)  {
            if (!wizard_image.base64)  {
