        if (wizard_image.base64)  {
            layerStack[0]=wizard_image.base64
        }
    }

    onMount(() => {
        quickStart()
    });
    let setServerToken = () => {      
        openProfile = false;
        if (!onPremise) {
            servertype='Gyre';
            serverurl="https://cloud.gyre.ai"
        }
    }
    function openLink(url) {
        window.open(url, '_blank')
    }
    afterUpdate(() => {
        let shadow = host.shadowRoot
        let links = shadow.querySelectorAll('.link')
        for (let l of links) {
            let url = l.getAttribute('href')
            l.addEventListener('click', () => {

                //  here we run modal of:
                // scene 3d primitive:
                //scene3DGetPrimitive = true;
                // search 3 d object dialog :
                //search3dopen = true;
                // modifiers :
                openmodifiers();


                openLink(url)
            })
        }
        let stack = shadow.querySelector('.stack')
        if (!stack) return
        if (stack.classList.contains("stackPos")) return
        let rect = stack.getBoundingClientRect();
    
        // Set initial position based on current position in the viewport
        fromX=  `${window.innerWidth - rect.right}px`;
        fromY = `${rect.top}px`;
        stack.classList.add("stackPos")
    })
    let fromX=0
    let fromY=0

    function openmodifiers(){
        // open modifier dialog
        selectModifier = true
        reopendialogevent = true
        setTimeout(() => {
            reopendialogevent = false
        }, '200')
    }

    let wizard=""
    let wizardCurrentStep=0
    let wizardData={}
    // start wizard
    function startWizard(type) {
        mode="wizard"
        wizard=type
        wizardData=wizards[type]
        data=null
        wizard_glb={}
        wizard_image={}
        wizard_pose={}
        wizard_text={string:""}
        wizard_prompt={text:""}
        wizard_modifier={}
        wizard_outpainting={}
        wizard_background={}
        layerStack=[]
        gotoWizardStep(0)
    }
    let wizardCurrentstepData={}
    let wizard_background={}

    async function next() {
        return await gotoWizardStep(wizardCurrentStep+1)
    }

    async function gotoWizardStep(num) {
        if (num<0) num=0

        // last step: finished wizard and jump to main application with generated document from collected data of wizard
        if (num>=wizardData.steps.length) {
//            wizard=""
//            quickStart()
         /*   fadeOut=true 
            stackTop=true
            await new Promise(resolve => setTimeout(resolve, 800))
            stackTopFadeOut=true
            await new Promise(resolve => setTimeout(resolve, 300))
*/
            let collectedData= {
                type: wizard,
                image: wizard_image,
                prompt:wizard_prompt,
                modifierInfo: wizard_modifier,
                scene3d: wizard_glb,
                pose: wizard_pose,
                text: wizard_text,
                outpainting: wizard_outpainting,
                background:wizard_background
            }
            let g=new gyreDocument()
            let doc=await g.fromWizard(collectedData)
            console.log(doc)
            file_uncompressed= doc              
            file_type="gyre"              
            initwidth = g.width
            initheight = g.height                
            file_name="From Wizard"
            if (wizard_image.name) file_name=wizard_image.name

            finishedInit=false
            return
        }
        wizardCurrentStep=num
        wizardCurrentstepData=wizardData.steps[num]
        if (num===1 && wizard==="background" && wizard_image.base64 && !wizard_background.base64) {  // special case: background removal
            let base64=wizard_image.base64
            base64=await convertToPNG(base64)
            let p={
                mode: "removeBackground",
                image64: base64.split(",")[1], // remove image prefix, pure base64 for AI
                taskid: (Math.random() + 1).toString(36).substring(3),   // need task-id
                prompt: ""
            }
            let g=new Gyre();
            g.runSD(SDConfig.generalConfig,p).then((result) => {
                if (!result || !result[0] || !result[0].base64) {                    
                    wizard_image={}
                    return
                }
                let img="data:image/png;base64,"+result[0].base64
                wizard_background={base64:img}
                next()
            })
        }
    }
    async function convertToPNG(base64Image) {
  return new Promise((resolve) => {
    // If it's already a PNG, just resolve with the input
    if (base64Image.includes("data:image/png")) {
      resolve(base64Image);
      return; // Exit early
    }
    const img = new Image();
    img.onload = () => {
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      // Set canvas dimensions to image dimensions (max 16MP)
      canvas.width = img.width;
      canvas.height = img.height;
      ctx.drawImage(img, 0, 0);
      const pngDataUrl = canvas.toDataURL('image/png')
      resolve(pngDataUrl)
    }
    img.src = base64Image;
  });
}

    let wizard_outpainting={}
    function setOutpaintingDirection(direction) {
        wizard_outpainting.direction=direction
        wizard_outpainting.wizard_outpainting
        next()

    }

    // set modifier for wizard
    let wizard_modifier={}
    function modifierSelected(el){
        wizard_modifier.name=el.detail.key
        wizard_modifier.modifiers=el.detail.modifiers.replace(/<br\/>/g, '\n')
        next()
    }
    // read pose  for wizard
    let wizard_pose={}
    let poseComponent

    async function posedowloaded(el){
        wizard_pose.thumb=el.thumb
        if (wizard_image.width) wizard_pose.width=wizard_image.width
        else wizard_pose.width=1024
        if (wizard_image.height) wizard_pose.height=wizard_image.height
        else wizard_pose.height=1024
        poseComponent.width=wizard_pose.width
        poseComponent.height=wizard_pose.height
        poseComponent.setScene(el)
        await new Promise(resolve => setTimeout(resolve, 510))
        wizard_pose.component=poseComponent
        next()

    }

    // read glb for wizard
    let wizard_glb={}
    let scene3dComponent
    async function glbbinarydowloaded(binary,thumb,searchstring){
        wizard_prompt.text=searchstring
        wizard_glb.thumb=thumb
        wizard_glb=wizard_glb
        let sceneparams = {meshes:[]};
        // eslint-disable-next-line no-undef
        if(BABYLON){
            sceneparams.camera = {};
            // eslint-disable-next-line no-undef
            sceneparams.camera.position = new BABYLON.Vector3(0, 1, 3)
            // eslint-disable-next-line no-undef
            sceneparams.camera.rotation = new BABYLON.Vector3(0, 0, 0)
        }
        await scene3dComponent.setScene(sceneparams)  // empty existing scene
        if (wizard_image.width) wizard_glb.width=wizard_image.width
        else wizard_glb.width=1024
        if (wizard_image.height) wizard_glb.height=wizard_image.height
        else wizard_glb.height=1024
        scene3dComponent.width=wizard_glb.width
        scene3dComponent.height=wizard_glb.height
        scene3dComponent.binary=binary  // add mesh
        wizard_glb.component=scene3dComponent
        next()
    }
     // read image as import for wizard
     let wizard_image={}
     async function importImageWizard(e) {
        const file = e.target.files[0]
        if (file.type && !file.type.startsWith('image/')) {
            alert('File is not an image.', file.type, file)
            return
        }
        wizard_image.name=file.name
        wizard_background={}        // new image: reset background information
        const reader = new FileReader()
        reader.addEventListener('load', async (event) => {
                wizard_image.base64= event.target.result                
                let img = new Image()
                img.src = wizard_image.base64
                await img.decode()
                wizard_image.width = img.width
                wizard_image.height = img.height         
                next()       
        })
        reader.readAsDataURL(file)        
    }
    // input text and font for wizard
    let textComponent
    let wizard_text={string:""}
    async function inputText(e) {
        wizard_prompt.text=e.target.value
        wizard_prompt.text= wizard_prompt.text.replace(/\n/g,  ' ')
        let upperText=e.target.value.toUpperCase()
   //    
        textComponent.width=1024
        textComponent.height=1024
        textComponent.text=upperText

        await textComponent.autoSize()  
        textComponent.background="white" 
        let base64=await textComponent.renderForAI()   
        textComponent.background="transparent" 

        wizard_text.string=upperText
        wizard_text.font_size=textComponent.font_size
        wizard_text.thumb=base64
        if (!wizard_text.font) wizard_text.font="Chewy"
        wizard_text.component=textComponent
        wizard_text=wizard_text
    }
    async function selectFont(font) {
