    }
    function quickStart() {
        mode="quickStart"
       data=quickStartData
    }    
    function newDocument() {
        mode="newDocument"
       data=newDocumentData

    }
    async function handleClick(entry) {
        if (entry.type==="file") {
            // load fdsai/gyre document from external URL
            if (entry.from==="external") {
                let response = await fetch(entry.file)
                const binaryData = await response.arrayBuffer()
                file_blob = new Blob([binaryData], { type: 'application/octet-stream' })
                file_name=entry.title
                file_type="gyre"
                finishedInit=false  // end init screen go top canvas
                return
            }
            // put local gyre file load here
            return
        }
        initwidth=entry.width
        initheight=entry.height
    }
    /**
     * read gyre document or image as import and stop intro screen and transfer image to canvas
     * @param e
     */
    async function importImage(e) {
        const file = e.target.files[0]
        if (file.type && !file.type.startsWith('image/') && !file.name.endsWith('.fdsai') && !file.name.endsWith('.gyre')) {
            alert('File is not an image.', file.type, file)
            return
        }
        file_name=file.name
        const reader = new FileReader()
        reader.addEventListener('load', async (event) => {
            if (file.name.endsWith('.fdsai') && !file.name.endsWith('.gyre')) {
                file_base64= event.target.result
                file_type="gyre"                
            } else {
                file_base64= event.target.result                
                file_type="image"
                let img = new Image()
                img.src = file_base64
                await img.decode()
                initwidth = img.width
                initheight = img.height                
            }
            finishedInit=false
        })
        reader.readAsDataURL(file)        
    }



    let onPremise=false

    let fadeOut=false 
    let stackTop=false
    let stackTopFadeOut=false

</script>
