        {:else}
        {category.intro}
        {/if}
        <div class="repeater" style="margin-bottom:20px">
            {#each category.entries as entry}
                <!-- external link -->
                {#if entry.url}
                <div class="repeater-item">                    
                    <div class="flexrow" class:square={entry.square}  >
                          <div class="imageel link" href="{entry.url}">              
                              <img class="imageelins" alt="Source" src="{entry.image}"/>      
                              {#if entry.imageHover}      
                                 <img class="imageelins result" alt="Result" src="{entry.imageHover}"/>
                              {/if}
                              {#if entry.play}
                                 <div class="play"></div>
                            {/if}
                          </div>
                    </div>
                    <div class="name" style="height:45px">{entry.title}</div>
                  </div>
                {/if}         
                <!-- wizard -->
                {#if entry.wizard}
                <div class="repeater-item">                    
                    <div class="flexrow" class:square={entry.square}  style="height:150px">
                          <!-- svelte-ignore a11y-click-events-have-key-events -->
