                              <img class="imageelins" alt="Source" src="{entry.image}"/>      
                              {#if entry.imageHover}      
                                 <img class="imageelins result" alt="Result" src="{entry.imageHover}"/>
                              {/if}
                          </div>
                    </div>
                    <div class="name" style="height:45px">{entry.title}</div>
                  </div>
                {/if}     
                <!-- open remote or local file -->
                {#if entry.file}
                <div class="repeater-item">                    
                    <div class="flexrow" style="width:207px;height:150px"  >
                          <div class="imageel" >              
                              <!-- svelte-ignore a11y-click-events-have-key-events -->
                              <img class="imageelins"style="width:207px" alt="{entry.name}" src="{entry.image}" on:click={() => { handleClick(entry)}} />
                          </div>
                    </div>
                    <div class="name" style="height:45px">{entry.title}</div>
                  </div>
                {/if}
                <!-- show icon for new document -->
                {#if entry.icon}
                <div class="repeater-item" style="margin:0; margin-top:20px">                    
                    <div class="flexrow" style="width:70px;height:70px;padding:0; margin:0"  >                 
                        <!-- svelte-ignore a11y-click-events-have-key-events -->
