                        size="50"
                        icon={entry.icon}
                        title={entry.title}
                        on:click={() => { handleClick(entry)}} 
                        ></fds-image-editor-button>
                    </div>
                    <div class="name" style="height:45px">{@html entry.title}</div>
                  </div>
                {/if}                
            {/each}
        </div>
    {/each}
    {/if}
