                                             if(servertype=='COMFY'){
                                                SDConfig.configList[1].generalConfig.url = serverurl;
                                             }
                                             if(servertype=='Gyre'){
                                                SDConfig.configList[0].generalConfig.url = serverurl;
                                             }

                                             servertype = e.target.value;

                                             if(servertype=='COMFY'){
                                                serverurl =   SDConfig.configList[1].generalConfig.url;
                                             }
                                             if(servertype=='Gyre'){
                                                serverurl =   SDConfig.configList[0].generalConfig.url;
