                                        >
                                        <!-- svelte-ignore a11y-click-events-have-key-events -->
                                        <fds-image-editor-button on:click={() => {onPremise=false }} type="button"
                                           >Gyre Cloud</fds-image-editor-button>                                                
                                    </div>
                                </div>



                                <div class="formLine"><label for="token">Token (optional)</label>
                                    <input lang="en" id="token" type="password" placeholder="Token" class="formInput"
                                        style="width:100%"
                                        bind:value={bearer}>
                                </div><div >
                                    {#if serverIsAvailable}
                                                                        <!-- svelte-ignore a11y-click-events-have-key-events -->
                                                                        <fds-image-editor-button on:click={setServerToken}  type="button"
                                                                        ><div  style="width:100px;text-align:center">Ok</div></fds-image-editor-button>   
                                    {:else}
                                    Server not available
                                    {/if}                                   
                                    </div>                                              
                            {:else}
                                <div class="formLine"><label for="token" >Gyre Cloud Token</label>
                                <div style="display:flex;  flex-flow: wrap; gap:10px;                               ">
                                              <input lang="en" type="password" placeholder="Token" id="token" class="formInput"
                                        style=" flex-grow: 1;"
                                        bind:value={bearer}>
                                        <!-- svelte-ignore a11y-click-events-have-key-events -->
                                        <fds-image-editor-button on:click={() => {onPremise=true }} type="button"
                                        style=" ">On Premise</fds-image-editor-button>    
                                        <div style="display:flex;  width:100%;">
                                        {#if serverIsAvailable}
                                           <!-- svelte-ignore a11y-click-events-have-key-events -->
                                            <fds-image-editor-button on:click={setServerToken}  type="button"  state="active"
                                            ><div  style="width:100px;text-align:center">Ok</div></fds-image-editor-button>   
                                        {:else}
                                        Input a valid token for access
                                        {/if}
                                        </div>
                                    </div>                  
                       
                                </div>
                              
                            {/if}
                           

