<svelte:options tag="fds-wizard-steps"></svelte:options>

<script>
export let steps=[]
export let current=0
let liWidth="25%"
$: {
    if (steps) {
        liWidth=100/steps.length+"%"
    }
}
</script>
<ol>
    {#each steps as step,i}
    <li class:current={current===i} style="width:{liWidth}">{step.text}</li>
    {/each}
  </ol>
