import config from "./ConfigClass";
import sdws from "./SDWebservice.js"
import {transferStore} from './TasksStore';
import promptClass from "./prompt.js"
import {serverurl} from "./FirstConfig.svelte";
import Comfy from "./Comfy";



class PostMessageAdapter {
    unsubscribe;
    openProfile = true;
    firstconfigelement;

    constructor() {}

    async postMessage(data) { 
        let result;

        if (data.key === "init") {
            // get server
            let initwidth = 512;
            let initheight = 512;
            let initurl = "";
            let initbearer = ""
            let Config = new config()
            await Config.load()
            await Config.get();
            //window.comfyuimode = true;
            /*
            if(window.comfyuimode){
                Config.generalConfig = JSON.parse(JSON.stringify(Config.generalConfig));
                Config.generalConfig.type='COMFY';
                Config.generalConfig.name='COMFY';
                Config.generalConfig.inpainting_original = false;
            } else {
                Config.generalConfig.type='SDGRPC';
                Config.generalConfig.name='Gyre';
                Config.generalConfig.inpainting_original = true;
            }
            */

            if (!Config.configList.length) {
                await Config.add('SDGRPC');
            }

            let hasComfy = Config.configList.find((el)=>{
                return el.generalConfig.name=='COMFY'
            })
            if(!hasComfy){
                await Config.add('COMFY');
                Config.currentConfigNum=0;
            }

            console.log("config");
            this.firstconfigelement = document.createElement('fds-first-config');
            this.firstconfigelement.classList.add("colorScheme")
            this.firstconfigelement.serverurl = Config.generalConfig.url || window.location.href;
            this.firstconfigelement.bearer = Config.generalConfig.token;
            this.firstconfigelement.SDConfig = Config
            this.firstconfigelement.servertype = Config.generalConfig.name;
            document.body.appendChild(this.firstconfigelement);
            if (!Config.configList.length) {
                this.firstconfigelement.finishedInit = true;
            }
            this.firstconfigelement.finishedInit = true

            if (!this.firstconfigelement.serverurl || (this.firstconfigelement.serverurl === "https://cloud.gyre.ai" && !this.firstconfigelement.bearer)) {
                this.firstconfigelement.openProfile = true
            }
            while (this.firstconfigelement.finishedInit) {
                await new Promise(r => setTimeout(r, 100));
            }



            console.log("Finished Init")
            initurl = this.firstconfigelement.serverurl || initurl;
            initurl = initurl.endsWith('/') ? initurl.slice(0, -1) : initurl;
            initwidth = this.firstconfigelement.initwidth || initwidth;
            initheight = this.firstconfigelement.initheight || initheight;
            initbearer = this.firstconfigelement.bearer

            let currentConfigNum =  0;

            Config.configList.forEach((el,idx)=>{
                if (el.generalConfig.name==this.firstconfigelement.servertype){
                    currentConfigNum=idx;
                }
            })


            console.log("current config number: ",Config.currentConfigNum);
            if(Config.currentConfigNum!=currentConfigNum){
                Config.currentConfigNum = currentConfigNum;
                await Config.get()
            }
            console.log("current config number after: ",Config.currentConfigNum);


            Config.generalConfig.url = initurl;
            Config.generalConfig.token = initbearer;
            await Config.set();
            //}

            var canvas = document.createElement("canvas");
            // set desired size of transparent image
            canvas.width = initwidth;
            canvas.height = initheight;
            let imageType = "png"
            let base64
            if (!this.firstconfigelement.file_blob && !this.firstconfigelement.file_uncompressed && !this.firstconfigelement.file_base64) {
                base64 = canvas.toDataURL()  // empty background layer
            }

            let blob
            let uncompressed
            // plain image as base64
            if (this.firstconfigelement.file_base64 && this.firstconfigelement.file_type === "image") {
                base64 = this.firstconfigelement.file_base64
                imageType = base64.substring("data:image/".length, base64.indexOf(";base64"))
            }
            // gyre document as base64
            if (this.firstconfigelement.file_base64 && this.firstconfigelement.file_type === "gyre") {
                base64 = this.firstconfigelement.file_base64
                imageType = "gyre"
            }
            // gyre document as binary 
            if (this.firstconfigelement.file_blob && this.firstconfigelement.file_type === "gyre") {
                blob = this.firstconfigelement.file_blob
                imageType = "gyre"
            }
            // gyre document as internal data/JSON structure
            if (this.firstconfigelement.file_uncompressed && this.firstconfigelement.file_type === "gyre") {
                uncompressed = this.firstconfigelement.file_uncompressed
                imageType = "gyre"
            }
            let WS = new sdws()
            let server_features = await WS.getServerFeatures(Config.generalConfig)
            console.log("server_features", server_features);

            result = {
                data:
                    {
                        base64,
                        blob,
                        imageType,
                        uncompressed,
                        key: "init",
                        documentInfo: {width: initwidth, height: initheight},
                        Config,
                        num: Config.currentConfigNum,
                        serverFeatures: server_features
                    }
            }
        }
        if (data.key === "changeModel") {
            result = await this.changeConfigElement({objkey: 'model', value: data.model});
        }

        if (data.key === "changeInpaintingModel") {
            result = await this.changeConfigElement({objkey: 'inpainting_model', value: data.model});
        }

        if (data.key === "changeGeneralConfig") {
            result = await this.changeConfigElement({objkey: data.type, value: data.value});
            if(data.skiprefresh) result=null;
        }


        if (data.key === "changeGlobalData") {
            let Config = new config()
            Config.updateGlobalData(data.globalData)
        }



        if (data.key === "changeSize") {
            let objkey;
            if (data.type === "width") {
                objkey = "width"

            }
            if (data.type === "height") {
                objkey = "height"
            }
            result = await this.changeConfigElement({objkey, value: data.value});
        }

        if (data.key === "numberImages") {

            let Config = new config()
            await Config.load()
            await Config.get()
            Config.generalConfig.dlgData.num = parseInt(data.num)
            Config.set()
        }

        if (data.key === "modifierSave") {
            let Config = new config()
            await Config.load()
            await Config.get();
            Config.ModifierData = JSON.parse(data.modifier.modifierdata);
            await Config.save();
        }


        if (data.key === "changeSampler") {
            let Config = new config()
            await Config.load()
            await Config.get()
            Config.generalConfig.dlgData.sampling_method = data.sampling_method
            Config.set()
        }


        if (data.key === "cancelRequest") {
            let WS = new sdws()
            await WS.cancelRequest(data.Config.generalConfig, data.taskid);
        }

        if (data.key === "getWorkflowsList") {
            let WS = new sdws()
            let workflowlist = await WS.getWorkflowList(data.Config.generalConfig)
            result = {
                data:
                    {
                        key: "workflowsListBack",
                        workflowlist
                    }
            };
        }


        if (data.key === "generate") {

            let layer = data.layer
            let p = new promptClass(layer.taskid)
            // @ts-ignore
            let prompt = await p.getPromptObject(layer.generate, layer.generate.mode, layer.ImageInfo, layer.AlphaImageInfo, layer.DepthImageInfo, layer.specialLayersImageInfo)
            let WS = new sdws();
            let layergeneration = layer.generate.layergeneration;
            prompt.layergeneration = layergeneration;
            let images = await WS.runSD(data.Config.generalConfig, prompt);


            if (!this.unsubscribe) {
                this.unsubscribe = transferStore.subscribe(async (taskel) => {
                    if (!taskel) return;
                    if (!taskel.taskid) return;
                    let res = taskel.answers.answer.map((el) => {
                        let im = el.artifacts[0];
                        //let base64 = "data:image/png;base64,"+im.binary;
                        let base64 = im.binary;
                        let seed = im.seed;
                        let complete = taskel.answers.complete;
                        let retobj = {base64, seed};
                        if (complete) retobj.complete = true;
                        return retobj;
                    })

                    window.postMessage({
                        key: "generateBack",
                        taskid: taskel.taskid,
                        imagelist: res,
                        generateasync: !window.generatesync
                    });

                    transferStore.set();
                });
            }


            if(images!=null) {
                result = {
                    data:
                        {
                            key: "generateBack",
                            taskid: layer.taskid,
                            generateasync: !window.generatesync,
                            imagelist: images,
                            layergeneration
                        }
                };
            }
        }


        if (result) window.postMessage(result.data);
    }

    async generate(config, layer) {
        let WS = new sdws()
        if (typeof layer === 'string' || layer instanceof String){
            const response = await fetch(layer);
            layer = await response.json();
        }

        let p = new promptClass(layer.taskid)
        // @ts-ignore
        let prompt = await p.getPromptObject(layer.generate, layer.generate.mode, layer.ImageInfo, layer.AlphaImageInfo, layer.DepthImageInfo, layer.specialLayersImageInfo,layer.ImageInfoSelection)

        let images = await WS.runSD(config, prompt);

        let result = {
                data:
                    {
                        key: "generateBack",
                        /*taskid: layer.taskid,*/
                        generateasync: !window.generatesync,
                        imagelist: images
                    }
        };

        if (result){
            return result.data;
        }
            //window.postMessage(result.data);
    }


    async changeConfigElement(element) {
        let Config = new config()
        await Config.load()
        await Config.get()
        if(element.objkey==""){
            Config.generalConfig = element.value;
        } else {
            Config.generalConfig[element.objkey] = element.value;
        }

        await Config.set()

        let WS = new sdws()
        let server_features = await WS.getServerFeatures(Config.generalConfig)
        console.log("server_features", server_features);

        let response = {
            data:
                {
                    key: "switchConfig",
                    num: Config.currentConfigNum,
                    serverFeatures: server_features
                }
        }
        return response;
    }

    getWorkflowImageRequestServerInstance() {
        let sdConfig = this.firstconfigelement?.SDConfig || null;
        return Comfy.getInstance(sdConfig);
    }

}

let inst = new PostMessageAdapter();
window.postMessageAdapter = inst;
export default inst;
