
export default class gyreDocument {

    constructor() {
        this.prompt=""
        this.modifiers=""
        this.presetName=""
        this.layers=[]
        this.width=this.height=1024
        this.id=1
    }
    setPrompt(p) {
        this.prompt=p
    }
    setModifiers(m,name) {
        this.modifiers=m
        this.presetName=name
    }
    setDefaultModifier() {
        this.modifiers="rawphoto of a...\n#-text\n-b&amp;w\n-bad art\n-poorly drawn\n-naked\n-close up\n-blurry\n-disfigured\n-bad anatomy\n-deformed\n-extra limbs\n-ugly\n-nude\nbeautiful\nhighly detailed\ncinematic\n#fujifilm\n#lora:https://civitai.com/api/download/models/16576:1\n#fuji"
        this.presetName="Cinematic"
    }
    layerImageEmpty() {
        let base64="data:image/webp;base64,"
        return this.layerImage(1024,1024,"Background Image",base64)
    }
    layer(type,name,width=0,height=0) {
        if (width) {
            this.width=width
            this.height=height
        }
        let l={
            id: this.id++,
            name:name,
            type:type,
            width:this.width,
            height:this.height,
            x:0,
            y:0,
            visible:true
        }
        return l
    }
    layerImage(width,height,name,base64) {
        let l=this.layer("image",name,width,height)
        
        l.url=base64
        return l
    }
    async layerText(width=1024,height=1024,name="Text",component,font_size,string,font,letter_spacing=0) {
        let l=this.layer("text",name,width,height)
        l.background_type = 'transparent'
        if (component) l.data = await component.getScene()
        l.font_size = font_size
        l.string = string
        l.letter_spacing = letter_spacing
        l.font = font
        return l
    }
    async layer3D(width=1024,height=1024,name='3D Scene',component) {
        let l=this.layer("3d",name,width,height)
        l.background_type = 'transparent'
        if (component) l.data = await component.getScene()
        return l
    }
    async layerPose(width=1024,height=1024,name='Pose',component) {
        let l=this.layer("posenet",name,width,height)
        l.background_type = 'transparent'
        l.show_character="human"
        if (component) l.data = await component.getScene()
        return l
    }
    setToolsLayers(x=0,y=0,width=1024,height=1024) {
        if (height>this.height) height=this.height
        if (width>this.width) width=this.width
        this.toolsLayers=[
            {
              "id": "inpaintingBrush",
              "type": "inpaintingBrush",             
            },
            {
              "id": "aiWindow",
              "type": "generationWindow",
              "x": x,
              "y": y,
              "width": width,
              "height": height,           
              "dragRestriction": true,
              "resizeRestriction": { "width": 1024, "height": 1024 }              
            },
            {
              "id": "aiSelection",
              "type": "aiSelection",
              "x": 0,
              "y": 0,
              "width": 512,
              "height": 512,
            }
        ]
        return 
    }
    extendImage(direction) {
        let orgWidth=this.width
        let orgHeight=this.height
        if (direction==="right") {
            this.width+=orgWidth/2
            this.setToolsLayers(orgWidth/2,0,orgWidth,orgHeight) 
        }
        if (direction==="bottom") {
            this.height+=orgHeight/2
            this.setToolsLayers(0,orgHeight,orgWidth,orgHeight) 
        }        
        if (direction==="left") {
            this.width+=orgWidth/2
            this.setToolsLayers(0,0,orgWidth,orgHeight) 
            this.layers[0].x=orgWidth/2
        }
        if (direction==="top") {
            this.height+=orgHeight/2
            this.setToolsLayers(0,0,orgWidth,orgHeight) 
            this.layers[0].y=orgHeight/2
        }                  
    }
    setMode(mode) {
        this.mode=mode
    }
    make() {
          
        let promptInfo = {
            prompt: this.prompt,
            modifiers: this.modifiers,
            presetName: this.presetName
        }
        let doc = {
            layers: this.layers.reverse(),
            document: {width: this.width, height: this.height},
            toolsLayers: this.toolsLayers,
            promptInfo
        }
        if (this.mode) doc.setMode=this.mode
        return doc
    }
    async fromWizard(collectedData) {
        if (collectedData.image.base64) {
            this.layers.push(this.layerImage(collectedData.image.width,collectedData.image.height,collectedData.image.name,collectedData.image.base64))
        } else {
            this.layers.push(this.layerImageEmpty())
        }
        if (collectedData.background.base64) {
            this.layers.push(this.layerImage(collectedData.image.width,collectedData.image.height,collectedData.image.name,collectedData.background.base64))
            this.layers[0].visible=false
            this.layers[1].subType="background"
            this.setMode("generationWindow")
        }
        if (collectedData.prompt) this.setPrompt(collectedData.prompt.text)
        if (collectedData.modifierInfo) this.setModifiers(collectedData.modifierInfo.modifiers,collectedData.modifierInfo.presetName)
        if (collectedData.scene3d.component) {
            this.layers.push(await this.layer3D(collectedData.scene3d.width,collectedData.scene3d.height,"3D Scene",collectedData.scene3d.component))
        }
        if (collectedData.pose.component) {
            this.layers.push(await this.layerPose(collectedData.pose.width,collectedData.pose.height,"Pose",collectedData.pose.component))
        }        
        if (collectedData.type==="text" && collectedData.text.component) {
            this.layers.push(await this.layerText(1024,1024,"Text",collectedData.text.component,collectedData.text.font_size,collectedData.text.string,collectedData.text.font))
            this.setMode("generationWindow")
        } 
        if (collectedData.type!=="txt2img") {
            this.setDefaultModifier()
        } else {
            this.setMode("generationWindow")
        }
        this.layers[this.layers.length-1].selected=true
        this.setToolsLayers()
        if (collectedData.type==="outpainting" && collectedData.outpainting.direction ) {
            this.extendImage(collectedData.outpainting.direction)
            this.setMode("generationWindow")
        } 
        
        return this.make()         
    }
}