import {litegraph}  from "../litegraph/litegraph.js";
let gyre;
export let convertToApiJson = async (datajson)=>{
    datajson = fixDataJson(datajson);
    let graph = new window.LGraph();
    graph.start();
    graph.configure(datajson);
    let res = await createApiJson(graph);
    return res;
}

let fixDataJson = (datajson)=>{
    datajson.nodes = datajson.nodes.map((el)=>{
        let widgets = datajson.extra.gyre.nodeWidgets[el.id];
        return{...el,widgets}
    })
    gyre = datajson.extra.gyre;
    return datajson;
}


let checkIsVirtualNode = (node)=> {
    let isVirtualNode = false;
    if(gyre.virtualNodes &&  gyre.virtualNodes.length){
        isVirtualNode =   gyre.virtualNodes.includes(node.type);
    }
    return isVirtualNode;
}


let prepareGraph = async(graph)=> {
    for (const outer of graph.computeExecutionOrder(false)) {
        if (outer.widgets) {
            for (const w of outer.widgets) {
                if(w.beforeQueued) w.beforeQueued();
            }
        }
        let  inner =  [outer];
        if(outer.getInnerNodes) {inner =  outer.getInnerNodes();}

        for (let n of inner) {
            if (checkIsVirtualNode(n) && n.applyToGraph) {
                n.applyToGraph();
            }
        }
    }
    return graph;
}

let createApiJson = async(graph)=> {

    graph = await prepareGraph(graph);
    const apiJsonOutput = {};


    for (const outerNode of graph.computeExecutionOrder(false)) {
        const skipNode = outerNode.mode === 2 || outerNode.mode === 4;
        const innerNodes = (!skipNode && outerNode.getInnerNodes) ? outerNode.getInnerNodes() : [outerNode];
        for (let node of innerNodes) {
            if (checkIsVirtualNode(node)){
                continue;
            }

            if ([2,4].includes(node.mode)) continue;

            let nodeInputs = {};
            let nodewidgets = node.widgets;


            if (nodewidgets) {
                for (let idx in nodewidgets) {
                    let  wdgt = nodewidgets[idx];
                    if (!wdgt.options || wdgt.options.serialize !== false) {
                        if(wdgt.serializeValue){
                            nodeInputs[wdgt.name] =   await wdgt.serializeValue(node, idx)
                        } else {
                            nodeInputs[wdgt.name] = wdgt.value;
                        }
                    }
                }
            }


            for (let idx in node.inputs) {
                let parentnode = node.getInputNode(idx);
                if (parentnode) {
                    let nodelink = node.getInputLink(idx);
                    while (parentnode.mode === 4 || checkIsVirtualNode(parentnode)) {
                        let nodefound = false;
                        if (checkIsVirtualNode(parentnode)){
                            nodelink = parentnode.getInputLink(nodelink.origin_slot);
                            if (nodelink) {
                                parentnode = parentnode.getInputNode(nodelink.target_slot);
                                if (parentnode) {
                                    nodefound = true;
                                }
                            }
                        } else if (nodelink && parentnode.mode === 4) {
                            let allInp = [nodelink.origin_slot, ...Object.keys(parentnode.inputs)];
                            for (let inpParent of allInp) {
                                if (parentnode.inputs[inpParent]?.type === node.inputs[idx].type) {
                                    nodelink = parentnode.getInputLink(inpParent);
                                    if (nodelink) {
                                        parentnode = parentnode.getInputNode(inpParent);
                                    }
                                    nodefound = true;
                                    break;
                                }
                            }
                        }

                        if (!nodefound) {
                            break;
                        }
                    }

                    if (nodelink && parentnode?.updateLink) {
                        nodelink = parentnode.updateLink(nodelink);
                    }

                    if (nodelink) {
                        nodeInputs[node.inputs[idx].name] = [String(nodelink.origin_id), parseInt(nodelink.origin_slot)];
                    }
                }
            }
            let ntitle = node.type;
            if(node.properties && node.properties['Node name for S&R']) ntitle = node.properties['Node name for S&R'];
            apiJsonOutput[String(node.id)] =  {
                inputs: nodeInputs,
                class_type: node.type,
                _meta: {
                    title: ntitle,
                }
            };
        }
    }

    for (let nId in apiJsonOutput) {
        for (const inputName in apiJsonOutput[nId].inputs) {
            const input = apiJsonOutput[nId].inputs[inputName];
            if (Array.isArray(input) && input.length === 2 && !apiJsonOutput[input[0]]) {
                delete apiJsonOutput[nId].inputs[inputName];
            }
        }
    }
    return  apiJsonOutput;
}


